<template>
  <view class="page">
    <!-- È°∂ÈÉ®Ê†è -->
    <view class="header">
      <text class="title">ËèúË∞±Â§ßÂÖ®</text>
      <text class="icon-search" @click="onSearchDialog">üîç</text>
    </view>

    <!-- ÊêúÁ¥¢Ê°ÜÔºàÈªòËÆ§ÈöêËóèÔºåÁÇπÂáªÊêúÁ¥¢ÂõæÊ†áÊòæÁ§∫Ôºâ -->
    <view class="search-container" v-if="showSearch">
      <input 
        class="search-input" 
        type="text" 
        placeholder="ÊêúÁ¥¢ËèúË∞±ÊàñÈ£üÊùê..." 
        v-model="searchKeyword"
        @confirm="doSearch"
      />
      <text class="cancel-btn" @click="showSearch = false">ÂèñÊ∂à</text>
    </view>

    <!-- ÂàÜÁ±ªÁ≠õÈÄâ -->
    <scroll-view class="chips" scroll-x>
      <view
        v-for="c in cats"
        :key="c.key"
        :class="['chip', activeCat === c.key ? 'active' : '']"
        @click="activeCat = c.key"
      >
        {{ c.name }}
      </view>
    </scroll-view>

    <!-- ‰∏§ÂàóÂç°ÁâáÁΩëÊ†º -->
    <view class="grid">
      <view class="card" v-for="r in filteredList" :key="r.id" @click="openRecipe(r)">
        <image class="cover" :src="r.cover" mode="aspectFill" />
        <view class="info">
          <text class="name">{{ r.name }}</text>
          <view class="meta">
            <view class="tag" :class="r.level === 'ÁÆÄÂçï' ? 'tag-green' : 'tag-orange'">{{ r.level }}</view>
            <text class="time">{{ r.time }}min</text>
          </view>
        </view>
      </view>
    </view>

    <!-- Á©∫Áä∂ÊÄÅÊèêÁ§∫ -->
    <view class="empty" v-if="filteredList.length === 0">
      <text>Ê≤°ÊúâÊâæÂà∞Áõ∏ÂÖ≥ËèúË∞±Âì¶~</text>
    </view>

    <view style="height: 24rpx;" />
  </view>
</template>

<script>
import BASE_URL from '../../config/api.js';

export default {
  data() {
    return {
      activeCat: 'all',
      cats: [
        { key: 'all', name: 'ÂÖ®ÈÉ®' }
        // ÂÖ∂‰ªñÂàÜÁ±ªÂ∞Ü‰ªéÊé•Âè£Âä†ËΩΩ
      ],
      list: [],          // ÊâÄÊúâËèúË∞±Êï∞ÊçÆ
      searchKeyword: '', // ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
      showSearch: false  // ÊòØÂê¶ÊòæÁ§∫ÊêúÁ¥¢Ê°Ü
    }
  },
  onLoad(options) {
    if (options && options.cat) {
      this.activeCat = options.cat;
    }
    this.loadCategories();
    this.loadRecipes();
  },
  onShow() {
    const cat = uni.getStorageSync('recipes_cat');
    if (cat) {
      this.activeCat = cat;
      uni.removeStorageSync('recipes_cat');
    }
  },
  computed: {
    filteredList() {
      // ‰ºòÂÖàÊòæÁ§∫ÊêúÁ¥¢ÁªìÊûú
      if (this.searchKeyword) {
        return this.list;
      }
      // ÂàÜÁ±ªÁ≠õÈÄâ
      if (this.activeCat === 'all') return this.list;
      return this.list.filter(i => i.cat.toString() === this.activeCat);
    }
  },
  methods: {
    // Âä†ËΩΩÂàÜÁ±ªÔºàÊéíÈô§Ê†πËäÇÁÇπÔºâ
    loadCategories() {
      console.log('ËØ∑Ê±ÇÂàÜÁ±ªURL:', `${BASE_URL}/api/recipes/categories`);
      uni.request({
        url: `${BASE_URL}/api/recipes/categories`,
        method: 'GET',
        success: (res) => {
          if (res.statusCode === 200 && res.data) {
            // ÂêàÂπ∂ÈªòËÆ§ÁöÑ"ÂÖ®ÈÉ®"ÈÄâÈ°πÂíåÊé•Âè£ËøîÂõûÁöÑÂàÜÁ±ª
            this.cats = [
              { key: 'all', name: 'ÂÖ®ÈÉ®' },
              ...res.data.map(type => ({
                key: type.nodeId.toString(),
                name: type.typeName
              }))
            ];
          }
        },
        fail: (err) => {
          console.error('Âä†ËΩΩÂàÜÁ±ªÂ§±Ë¥•', err);
        }
      });
    },
    
    // Âä†ËΩΩËèúË∞±Êï∞ÊçÆ
    loadRecipes() {
      const categoryId = this.activeCat === 'all' ? '' : this.activeCat;
      console.log('ËØ∑Ê±ÇËèúË∞±URL:', `${BASE_URL}/api/recipes?categoryId=${categoryId}`);
      uni.request({
        url: `${BASE_URL}/api/recipes?categoryId=${categoryId}`,
        method: 'GET',
        success: (res) => {
          if (res.statusCode === 200 && res.data) {
            this.list = res.data.map(recipe => ({
              id: recipe.recipeId,
              name: recipe.name,
              level: recipe.difficulty || 'ÁÆÄÂçï',
              time: recipe.cookTime || Math.floor(Math.random() * 30) + 10,
              cat: recipe.cuisineType,
              cover: recipe.coverImg || 'https://img.js.design/assets/img/6638d48432d24d4ad14381c3.png'
            }));
          }
        },
        fail: (err) => {
          console.error('Âä†ËΩΩËèúË∞±Â§±Ë¥•', err);
        }
      });
    },
    
    // ÊòæÁ§∫ÊêúÁ¥¢Ê°Ü
    onSearchDialog() {
      this.showSearch = true;
      // Ëá™Âä®ËÅöÁÑ¶ÔºàÈúÄË¶ÅÈÖçÂêàÁªÑ‰ª∂ÂÆûÁé∞Ôºâ
      setTimeout(() => {
        // Ê≥®ÊÑèÔºöÂú®uni-app‰∏≠ÔºåÁõ¥Êé•ËÅöÁÑ¶ÂèØËÉΩ‰∏çÁîüÊïàÔºåÈúÄË¶Å‰ΩøÁî®refÊñπÂºè
      }, 100);
    },
    
    // ÊâßË°åÊêúÁ¥¢
    doSearch() {
      if (!this.searchKeyword.trim()) {
        uni.showToast({ title: 'ËØ∑ËæìÂÖ•ÊêúÁ¥¢ÂÜÖÂÆπ', icon: 'none' });
        return;
      }
      
      uni.request({
        url: `${BASE_URL}/api/recipes/search?keyword=${encodeURIComponent(this.searchKeyword)}`,
        method: 'GET',
        success: (res) => {
          if (res.statusCode === 200 && res.data) {
            this.list = res.data.map(recipe => ({
              id: recipe.recipeId,
              name: recipe.name,
              level: recipe.difficulty || 'ÁÆÄÂçï',
              time: recipe.cookTime || Math.floor(Math.random() * 30) + 10,
              cat: recipe.cuisineType,
              cover: recipe.coverImg || 'https://img.js.design/assets/img/6638d48432d24d4ad14381c3.png'
            }));
          }
        },
        fail: (err) => {
          console.error('ÊêúÁ¥¢Â§±Ë¥•', err);
          uni.showToast({ title: 'ÊêúÁ¥¢Â§±Ë¥•', icon: 'none' });
        }
      });
    },
    
    // ÊâìÂºÄËèúË∞±ËØ¶ÊÉÖ
    openRecipe(r) {
      uni.navigateTo({ url: `/pages/recipe/detail?id=${r.id}` });
    }
  },
  watch: {
    activeCat() {
      // ÂàáÊç¢ÂàÜÁ±ªÊó∂Ê∏ÖÁ©∫ÊêúÁ¥¢ÂÖ≥ÈîÆËØç
      this.searchKeyword = '';
      this.loadRecipes();
    }
  }
}
</script>

<style>
